import {
  CREATE_DONE,
  INPUTTING,
} from './actions'

export default (state, action) => {
  switch (action.type) {
    case CREATE_DONE:
      const dones = [action.payload, ...state.dones]
      return { ...state, dones: dones }
    case INPUTTING:
      return { ...state, inputting: action.payload }
    default:
      return state
  }
}



