import React, { Component } from 'react';
import { connect } from 'react-redux'
import './App.css';

import Hero from './components/Hero'
import Register from './components/Register'
import List from './components/List'

import {
  createDone,
  inputtingDone,
} from './actions'

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="App-div">
          <Hero data={ this.props.inputting } />
        </div>
        <div>
          <Register inputHandle={ this.props.inputtingDone }
                    submitHandle={ this.props.createDone } />
          <List dones={ this.props.dones }/>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    newDone: state.newDone,
    inputting: state.inputting,
    dones: state.dones
  }
}

const mapDispatchToProps = dispatch => {
  return {
    createDone: done => {
      dispatch(createDone(done))
    },
    inputtingDone: done => {
      dispatch(inputtingDone(done))
    }
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App)



