import React, { Component } from 'react';

import './List.css'

class List extends Component {
  render () {
    const items = this.props.dones.map((item, idx) => {
      return <li className="done-item" key={idx}>{ item }</li>
    })

    return (
      <ul className="done-list">
        {items}
      </ul>
    )
  }
}

export default List



